<template>
  <div>
    <div class="qrcode" ref="qrCodeUrl"></div>
    看这个二维码真他妈漂亮！！！！！！！
  </div>
</template>


<script>
  import QRCode from 'qrcodejs2'
export default {
  methods:{
    creatQrCode() {
      var qrcode = new QRCode(this.$refs.qrCodeUrl, {
        text: '漂亮吗', // 需要转换为二维码的内容
        width: 100,
        height: 100,
        colorDark: '#000000',
        colorLight: '#ffffff',
        correctLevel: QRCode.CorrectLevel.H
      })
    }
  },
  mounted() {
    this.creatQrCode();
  }
}
</script>

<style scoped>
  div{
    font-weight: bold;
    font-size: 50px;
  }
</style>
