<template>
  <div>

    <Post :item="PostData" :is-detail="true">
    </Post>

    <div id="inputComment" class="inputComment">
      <div style="margin-bottom: 10px;">发表评论</div>
      <div class="textComment">
        <span style="color: #828282">您的名字：</span>
        <input v-model="CommentName" type="text"
               style="border-radius: 5px;border: 1px solid #999;height: 25px;padding: 10px">
        <textarea v-model="CommentContent" placeholder="请在此处填写评论" name="inputComment" rows="8"></textarea>
      </div>
      <button @click="addComment">发布评论</button>
    </div>
    <!--    <div class="inputCommentAll">-->
    <!--    </div>-->
    <div class="comment">
      <div class="CommentTitle">评论(共{{CommentTotal}}条)</div>
      <ul :data="PostComment">
        <li>
          <div v-for="(item,index) in PostComment.comments" :key="item.id">
            <a href="/">{{item.name}}：</a>
            <div style="font-size: 14px">{{item.content}}</div>
            <div style="font-size: 12px">{{toDates(item.create_time)}}</div>
          </div>
        </li>
      </ul>
    </div>
    <div class="moreComment">暂无更多评论</div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        id: this.$route.params.id,
        PostData: {},
        //后端返回的评论内容
        PostComment: {},
        // 页数
        page_index: 1,
        // 一页包含的内容个数
        page_size: 10,
        // 评论总数
        CommentTotal: 0,
        // 评论人名字
        CommentName: '',
        // 评论内容
        CommentContent: '',
        // 当前页面地址
        PostDetailHref: ''
      }
    },
    created () {
      this.getPostData()
      this.getComment()
      this.getCommentTotal()
      this.getHref()
    },
    methods: {
      // 获取评论
      async getComment () {
        const { data: res } = await this.$http.get('comment', {
          params: {
            type: 1,
            p_id: this.id,
            page_index: this.page_index,
            page_size: this.page_size
          }
        })
        this.PostComment = res
      },
      // 获取评论总数
      async getCommentTotal () {
        const { data: res } = await this.$http.get('comment', {
          params: {
            type: 3,
            p_id: this.id
          }
        })
        this.CommentTotal = res.count
        this.getComment()
      },

      //发布评论
      async addComment () {
        var name = this.CommentName
        var content = this.CommentContent
        if (name === '' || content === '') {
          this.$message.error('请输入名字或者评论内容')
          return 'error'
        }
        const { data: res } = await this.$http.post('comment', {
          'p_id': this.PostData.id,
          'name': name,
          'content': content
        })
        console.log(res)
        this.getComment()
      },

      // 根据id获取帖子数据
      async getPostData () {
        const { data: res } = await this.$http.get('post', {
          params: {
            'type': 2,
            'id': this.id
          }
        })
        if(res.code===1) {
          this.PostData = res
        }
        else{
          console.error(res.message)
        }
      },
      getHref(){
        this.PostDetailHref = window.location.href
        console.log(this.PostDetailHref)
      }
    }
  }
</script>

<style scoped>
  /* 评论区整体样式 */
  .comment {
    padding: 0 20px 0 20px;
    background-color: #fff;
  }

  /* 评论标题样式 */
  .CommentTitle {
    padding-bottom: 15px;
    border-bottom: 1px solid black;
    display: flex;
    justify-content: center;
    background-color: #fff;
  }

  .comment ul li > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 0 20px 0;
    padding-bottom: 20px;
    border-bottom: 1px solid #999;
  }

  .comment ul li > div:last-child {
    margin-bottom: 0;
  }

  .comment ul li > div a {
    text-decoration: none;
    font-weight: bold;
    color: #3B6FA8;
  }

  .moreComment {
    background-color: #fff;
    border-radius: 0 0 10px 10px;
    display: flex;
    justify-content: center;
    padding-top: 20px;
    padding-bottom: 20px;
    color: #828282;
    font-size: 13px;
    font-weight: bold;
  }

  /* 输入框的样式 */
  /*.inputCommentAll{*/
  /*  background-color: #fff;*/
  /*}*/
  .inputComment {
    width: 500px;
    background-color: #fff;
    text-align: center;
    padding: 20px;
    border-radius: 10px 10px 0 0;
  }

  .inputComment textarea {
    height: 100px;
    width: 90%;
    /* 规定能否拉伸样式 */
    resize: none;
    border: 1px solid #999;
    border-radius: 8px;
    margin: 10px;
    padding: 15px;
  }

  .inputComment button {
    background-color: #7482e1;
    color: white;
    border: 1px solid #999;
    border-radius: 8px;
    padding: 5px;
  }

  .inputComment button:hover {
    background-color: #999;
    cursor: pointer;
  }
</style>
